import { useEffect } from 'react';
import useDyslexia from './useDyslexia';

const Dyslexia: React.FC = () => {
    useEffect(() => {
        const pid = useDyslexia('p, h1, h2, h3, h4, h5, h6, blockquote, cite, q');
        return () => clearInterval(pid);
    }, []);

    return <>
        <h1>Qu'est-ce que la dyslexie?</h1>
        <div>
            <p>La dyslexie est un trouble d'apprentissage d'origine neurologique. Il s'agit donc d'un problme de fonctionnement dans le cerveau, et non d'un problme d'intelligence ni de stimulation. Le cerveau des personnes prsentant une dyslexie a de la difficult  percevoir et  analyser de faon prcise et rapide les sons dans les mots, alors que les autres zones du cerveau fonctionnent normalement.</p>
            <p>
                Un enfant qui souffre de dyslexie a ainsi de la difficult  reconnatre les mots crits. En lisant, il oublie parfois des lettres, les inverse ou les remplace par d'autres sans faire exprs, ce qui nuit  la vitesse et  la prcision de sa lecture.
                Certains enfants dyslexiques ne font toutefois pas d'erreurs en lisant, mais leur vitesse de lecture est trs lente. Dans tous les cas, l'enfant qui souffre de dyslexie peut avoir de la difficult  comprendre ce qu'il lit, peu importe qu'il lise lentement ou vite, qu'il fasse beaucoup d'erreurs ou non.
            </p>
            <p>Par contre, les enfants qui ont des difficults en lecture ne prsentent pas tous une dyslexie. Certains, par exemple, ont un trouble du langage oral (ex. : trouble dveloppemental du langage autrefois appel  dysphasie ) qui a des rpercussions sur leur apprentissage du langage crit. Par ailleurs, les enfants prsentant une dyslexie n'ont pas ncessairement de difficult  comprendre et  parler avant 5 ans, mais si c'est le cas, ces troubles sont souvent lgers.</p>
        </div>
    </>;
};

export default Dyslexia;
